
import React from "react";

export default function Contact() {
    // for submitting contact form
    const [name, setName] = React.useState("");
    const [email, setEmail] = React.useState("");
    const [message, setMessage] = React.useState("");

    function encode(data) {
        return Object.keys(data).map((key) => encodeURIComponent(key) + "=" + encodeURIComponent(data[key])).join("&")
    }

    function handleSubmit(event) {
        event.preventDefault();
        fetch("/", {
            method: "POST",
            headers: encode({ "Content-Type": "application/x-www-form-urlencoded"})
        }).then(() => alert("Message sent!")).catch((error) => error)
    }

    return (
        <section id="contact" className="relative">
            {/* <div className="container px-5 py-10 mx-auto flex sm:flex-nowrap flex-wrap"> */}
            <div className="container px-5 py-10 mx-auto">
                {/* <div className="lg:w-2/3 md:w-1/2 bg-gray-900 rounded-lg overflow-hidden sm:mr-10 p-10 flex items-end justify-start relative"> */}
                    {/* <div className="lg:w-2/3 md:w-1/2 bg-gray-900 rounded-lg overflow-hidden sm:mr-10 p-10 flex items-end justify-start relative">
                        <iframe width="100%" height="100%" title="map" className="absolute inset-0" frameBorder={0} marginHeight={0} marginWidth={0} style={{ filter: "opacity(0.7)" }} src="https://www.google.com/maps/embed/v1/place?q=97+warren+st+new+york+city&key=AIzaSyBFw0Qbyq9zTFTd-tUY6dZWTgaQzuU17R8" />
                        <div className="bg-gray-900 relative flex flex-wrap py-6 rounded shadow-md">
                            <div className="lg:w-1/2 px-6">
                                <h2 className="title-font font-semibold text-white tracking-widest text-xs">
                                    ADDRESS
                                </h2>
                                <p className="mt-1">
                                    Houston, Texas
                                </p>
                            </div>
                            <div className="lg:w-1/2 px-6 mt-4 lg:mt-0">
                                <h2 className="title-font font-semibold text-white tracking-widest text-xs">
                                    E-MAIL
                                </h2>
                                <a className="text-indigo-400 leading-relaxed">
                                    helloidaworld@gmail.com
                                </a>
                                <h2 className="title-font font-semibold text-white tracking-widest text-xs mt-4">
                                    PHONE
                                </h2>
                                <p className="leading-relaxed">
                                    777-777-77777
                                </p>
                            </div>
                        </div>
                    </div> */}
                    {/* <form netlify name="contact" className="lg:w-1/3 md:w-1/2 flex flex-col md:ml-auto w-full md:py-8 mt-8 md:mt-0" onSubmit={handleSubmit}> */}
                    <form netlify name="contact" className="flex flex-col md:ml-auto w-full md:py-8 mt-8 md:mt-0" onSubmit={handleSubmit}>
                        <div className="text-center">
                          
                            <h2 className="text-white sm:text-4xl text-3xl mb-1 font-medium title-font">
                                Send Message! 
                            </h2>
                            <p className="leading-relaxed mb-5">
                            I want to hear from you! 
                            </p>
                        </div>
                        <div className="relative mb-4">
                            <label htmlFor="name" className="leading-7 text-sm text-gray-400">
                                name
                            </label>
                            <input type="text" id="name" name="name" className="w-full bg-gray-800 rounded border border-gray-700 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-900 text-base outline-none text-gray-100 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"  onChange={(event) => setName(event.target.value)} />
                        </div>
                        <div className="relative mb-4">
                            <label htmlFor="email" className="leading-7 text-sm text-gray-400">
                                e-mail
                            </label>
                            <input type="email" id="email" name="email" className="w-full bg-gray-800 rounded border border-gray-700 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-900 text-base outline-none text-gray-100 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"  onChange={(event) => setEmail(event.target.value)} />
                        </div>
                        <div className="relative mb-4">
                            <label htmlFor="message" className="leading-7 text-sm text-gray-400">
                                message
                            </label>
                            <textarea id="message" name="message" className="w-full bg-gray-800 rounded border border-gray-700 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-900 text-base outline-none text-gray-100 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" onChange={(event) => setMessage(event.target.value)} />
                        </div>
                        <button type="submit" className="text-white bg-indigo-500 border-0 py-2 px-6 focus:outline-none hover:bg-indigo-600 rounded text-lg">
                            send!
                        </button>
                    </form>
                </div>
            {/* </div> */}
        </section>
    );
}


